import React from 'react';
import styled from 'styled-components';
import { NavLink } from "react-router-dom";

const SNavItemDropdown = styled.div`
  display: ${({ isNavItemOpen }) => isNavItemOpen ? 'block' : 'none'};
  position: absolute;
  padding-top: 1.5rem;
  top: 0;
  left: 0;
  z-index: 100;
`

const SNavItemContainer = styled.div`
  display: flex;  
  flex-direction: column;
  background: #24231D;
  padding: 2px 10px 14px;
  margin-left: -6px;
  z-index: 100;
`

const NavItemDropdown = ({ isNavItemOpen, context }) => {
    switch(context) {
      case "about": return (
        <SNavItemDropdown isNavItemOpen={isNavItemOpen}>
          <SNavItemContainer>
            <NavLink
              to="/overview"
              className="Nav__link"
              isActive={(match) => (!match || !match.isExact) ? false : true}
            >
              Overview
            </NavLink>
            <a
              target="_blank" 
              rel="noreferrer"
              href="https://docs.inertia.supply"
              className="Nav__link"
            >
              Docs
            </a>
            <NavLink
              to="/token"
              className="Nav__link"
              isActive={(match) => (!match || !match.isExact) ? false : true}
            >
              INR Token
            </NavLink>
            <NavLink
              to="/faq"
              className="Nav__link"
              isActive={(match) => (!match || !match.isExact) ? false : true}
            >
              FAQ
            </NavLink>
            <a href="https://www.medium.com" rel="noreferrer" target="_blank" className="Nav__link">
              Blog
            </a>
          </SNavItemContainer>
        </SNavItemDropdown>
      );
      case "developers": return (
        <SNavItemDropdown isNavItemOpen={isNavItemOpen}>
          <SNavItemContainer>
            <a href="https://github.com/inertia-supply" target="_blank" rel="noreferrer" className="Nav__link">
              Contracts
            </a>
            <NavLink
              to="/bounty"
              className="Nav__link"
              isActive={(match) => (!match || !match.isExact) ? false : true}
            >
              Bug Bounty
            </NavLink>
          </SNavItemContainer>
        </SNavItemDropdown>
      );
      case "community": return (
        <SNavItemDropdown isNavItemOpen={isNavItemOpen}>
          <SNavItemContainer>
            <a href="https://whitepaper.inertia.supply" target="_blank" rel="noreferrer" className="Nav__link">
              Discord
            </a>
            <a href="https://whitepaper.inertia.supply" target="_blank" rel="noreferrer" className="Nav__link">
              Telegram
            </a>
            <a href="https://whitepaper.inertia.supply" target="_blank" rel="noreferrer" className="Nav__link">
              Twitter
            </a>
          </SNavItemContainer>
        </SNavItemDropdown>
      );
      default: return null;
    }
}

export default NavItemDropdown